<template>
  <div id="app">
    <div class="content staggering-grid" v-if="isShowGridBackground">
      <div class="grid">
        <div
          v-for="index in 2400"
          :key="index"
          class="small square el"
          style="transform: scale(1);"
        ></div>
      </div>
    </div>
    <div class="content line-drawing">
      <profile />
      <code-pen />
      <git-hub :change-style="changeStyle" />
      <vue-js :change-style="changeStyle" />
    </div>
  </div>
</template>

<script>
import Profile from "@/components/Profile";
import CodePen from "@/components/CodePen";
import GitHub from "@/components/GitHub";
import VueJs from "@/components/VueJs";
export default {
  name: "App",
  components: {
    Profile,
    CodePen,
    GitHub,
    VueJs
  },
  data() {
    return {
      isShowGridBackground: true,
      gitHubFill: "none",
      gitHubStroke: "currentColor",
      changeStyle: false
    };
  },
  methods: {
    approachAnime() {
      this.$anime({
        targets: ".staggering-grid .el",
        scale: [
          { value: 0.1, easing: "easeOutSine", duration: 500 },
          { value: 1, easing: "easeInOutQuad", duration: 1200 }
        ],
        delay: this.$anime.stagger(20, { grid: [100, 5], from: "center" })
      });
      setTimeout(() => {
        this.isShowGridBackground = false;
        this.$anime({
          targets: ".line-drawing .lines path",
          strokeDashoffset: [this.$anime.setDashoffset, 0],
          easing: "easeInOutQuad",
          duration: 1500,
          delay: function(el, i) {
            return i * 250;
          },
          direction: "alternate",
          loop: false,
          complete: () => {
            this.changeStyle = true;
          }
        });
      }, 3000);
    }
  },
  mounted() {
    this.approachAnime();
  }
};
</script>
<style lang="scss" scoped>
@import "@/assets/fonts/fonts.scss";
#app {
  text-align: center;
  .content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;
    height: 100vh;
  }
  .staggering-grid {
    .grid {
      display: flex;
      flex-wrap: wrap;
      .small {
        width: 18px;
        height: 18px;
      }
      .square {
        pointer-events: none;
        position: relative;
        width: 28px;
        height: 28px;
        margin: 1px;
        background: radial-gradient(#333, #0b1114);
        font-size: 14px;
      }
    }
  }
}
</style>
